* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: #fafafa;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen-Sans, Ubuntu, Cantarell, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

img {
  max-width: 100%;
  display: block;
}

.container {
  justify-content: center;
  display: flex;
}

.btn:first-child {
  margin-right: 40px;
}

.btn {
  color: rgba(255, 255, 255, .47);
  width: 120px;
  height: 120px;
  text-align: center;
  vertical-align: middle;
  cursor: pointer;
  background: #8cd460;
  border: 2px solid rgba(255, 255, 255, .47);
  border-radius: 50%;
  font-weight: bold;
  line-height: 120px;
  text-decoration: none;
  transition: all 4s;
  display: inline-block;
  overflow: hidden;
  box-shadow: 0 0 0 5px #8cd460;
}

.start {
  background: #8cd460;
  box-shadow: 0 0 0 5px #8cd460;
}

.stop {
  background: #ff8181;
  box-shadow: 0 0 0 5px #ff8181;
}

.btn:hover {
  border-style: dashed;
}

.disabled {
  cursor: not-allowed;
  background-color: #a8a7a7;
  box-shadow: 0 0 0 5px #a8a7a7;
}

